.postsBlock {
}

.posts {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1), 0 5px 15px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
    .postsBlock, .posts {
        padding: 0;
        box-shadow: none;
    }
}