.profileInfoWrapper {
    position: relative;
    margin-bottom: 20px;
    padding: 40px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1), 0 5px 15px rgba(0, 0, 0, 0.2);
}
.descriptionBlock {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 40px;
}
.statusAndAvaBlock{
    position: relative;
}
.statusMain{
    position: absolute;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
}
.inputContainer {
    position: relative;
}
.mainPhoto {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    transition: transform 0.2s ease-in-out;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1), 0 5px 15px rgba(0, 0, 0, 0.2);
}
.mainPhotoInput {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}
.mainPhoto:hover {
    transform: scale(1.1);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2), 0 10px 30px rgba(0, 0, 0, 0.3);
}
.contacts{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1), 0 5px 15px rgba(0, 0, 0, 0.2);
    gap: 10px;
}
.editProfileInfo{
    position: absolute;
    top: 5px;
    left: 5px;
}
.edit{
    transform: rotate(45deg);
    width: 18px;
}

@media (max-width: 768px) {
    .profileInfoWrapper {
        width: 90%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px;
        margin-bottom: 20px;
    }
    .statusAndAvaBlock{
        display: flex;
        justify-content: center;
        margin-bottom: 0;
    }
    .descriptionBlock {
        flex-direction: column;
        width: 100%;
        margin-bottom: 0;
    }
    .contacts{
        box-shadow: none;
        justify-content: center;
        width: 90%;
        padding: 0;
    }
    .statusMain {
        bottom: 0;
    }
    .mainPhoto {
        width: 150px;
        height: 150px;
    }
}